.layout-c {

    .page-header {

      @include media($mobile) {
         padding-right: 110px;
      }

      @include media($small) {
          @include padding-right(20%);
      }

      @include media($medium) {
          @include padding-right(25%);
      }

      .page-header-body,
      .page-header-actions {

         @include media($medium) {
            margin-top: 0;
         }
      }
   }

   .vertical-tabs {

      @include media($medium) {
         padding-left: $base-spacing;
      }
   }

   .vertical-tabs-content {

      .post-field {
         max-width: 600px;

         @include media($large) {
            max-width: 75%;
         }

         &:last-child {
            margin-bottom: 0;
         }
      }
   }

}
